import React from 'react'
import axios from 'axios'
import { withRouter } from 'next/router'

import Base from '../layouts/base'
import Link from 'next/link'
import Head from 'next/head'

import Tag from '../components/Tag'


class Index extends React.Component {
  constructor() {
    super()
    this.state = {
      topics: [],
      conferences: [],
    }
  }

  static getInitialProps(ctx) {
    const topic = ctx.query.topic
    console.log('topic', topic)
  }

  componentDidMount() {
    const url = 'http://localhost:3001/topics'

    axios
    .get(url)
    .then(response => {
      const topics = response.data
      this.setState({ topics })
    })
    .catch(error => {
      console.log('error', error)
    })

    const topic = this.props.router.query.topic
    const urlConferences = 'http://localhost:3001/conferences?topics_like=' + topic

    axios
    .get(urlConferences)
    .then(response => {
      const conferences = response.data
      this.setState({ conferences })
    })
    .catch(error => {
      console.log('error', error)
    })
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.router.query.topic !== this.props.router.query.topic) {
      const topic = nextProps.router.query.topic
      const urlConferences = 'http://localhost:3001/conferences?topics_like=' + topic

      axios
      .get(urlConferences)
      .then(response => {
        const conferences = response.data
        this.setState({ conferences })
      })
      .catch(error => {
        console.log('error', error)
      })
    }
  }

  render() {
    const topic = this.props.router.query.topic
    const { topics, conferences } = this.state
    return (
      <Base>
        <Head>
          <title>Confs {topic}</title>
        </Head>
        <div className="container">
          <header className="header">
            <h1>Confs {topic}</h1>
          </header>
          <div className="row">
            <aside className="sidebar">
              <div>
                <h5 className="subtitle">
                  Topics
                </h5>
                <ul className="tags">
                  { topics.map(topic => <Tag key={ topic } topic={topic} />) }
                </ul>
              </div>
              <div>
                <h5 className="subtitle">
                  About
                </h5>
                <ul className="links">
                  <li className="link">
                    <Link href="/about">
                      <a>About</a>
                    </Link>
                  </li>
                  <li className="link">
                    <Link href="/privacity">
                      <a>Privacity</a>
                    </Link>
                  </li>
                </ul>
              </div>
            </aside>
            <main className="main">
              <h5 className="subtitle">
                Conferences
              </h5>

              { conferences.map(conference => <article className="article">
                <h2 className="article-title">
                  <a ahref={ conference.url }>
                    { conference.title }
                  </a>
                </h2>
                <p className="article-description">{ conference.place } | { conference.start } - { conference.end }</p>
  
                <div className="tags">
                  {
                    conference.topics.map(topic => <Tag key={ topic } topic={ topic } />)
                  }
                </div>
              </article>) }
            </main>
          </div>
        </div>
      </Base>
    )
  }
}

export default withRouter(Index)


